      // overflow: hidden;
    }
  }

  .app-header.affix {
    @include mobile {
      padding: 30px !important;
    }
  }

  .main-shared {
    @include mobile {
      padding-bottom: 0;
    }
  }
  
  #logo-img, .desktop-navigation-links {
    @include mobile {
      display: none;
    }
  }
  
  .main-header {
    .btn {
      color: $color-header;
      border-color: $color-header;
    }

    h2 {
      font-family: 'OpenSansHebrew';
    }
  }
  
    main {
